.users{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.users>p,.users>h2,.users>table{
    width: 80%;
    margin: 15px 0px;
}
.table>thead{
    background-color: black;
    color: white;
}
.table th{
    padding: 7px 0px 7px 5px;
}
.table td{
    text-align: center;
}
